.has-error {
  border-color: red;
  background-color: #fff0e8;
}

.has-error:focus {
  color: red;
  border-color: red;
}

.card-body {
  margin-top: -15px;
}

.card-panel {
  background-color: transparent;
}

.small .btn-sm {
  padding: .2rem .4rem;
  font-size: .75rem;
  line-height: 1.25;
  border-radius: .25rem;
}

.form-check-right {
  margin-right: 1.1rem;
}

.form-check-right input {
  margin-left: 0.5rem;
}

.field-required {
  font-style: italic;
  color: red;
  font-size: smaller;
}